.modal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: none;
    background: rgba(0, 0, 0, 0.5);
}
.active {
    display: flex;
    justify-content: center;
    align-items: center;
}
.modalContent {
    padding: 25px;
    background: white;
    border-radius: 15px;
    min-width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}