.noteCard {
    padding: 10px;
    width: 250px;
    min-height: 100px;
    border-radius: 20px;
    border: black solid 1px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    box-sizing: border-box;
}
.title {
    margin: 0 0 10px;
}
.status {
    margin-bottom: 10px;
}
.content {
    overflow-wrap: anywhere;
}
